
$dblue: rgb(106, 130, 183);
$wine: rgb(240, 237, 248);
$lorange: rgb(251, 169, 44);
$peach: rgb(252, 112, 97);
$brown: rgb(74, 62, 66);
$white: rgb(252, 244, 239);
$navy: rgb(54, 66, 92);
$grey: rgb(114, 111, 121);
$lpurple: rgb(170, 135, 155);
$lgreen: rgb(140, 163, 163);
$dgreen: rgb(72, 79, 79);
$lblue: rgb(175, 194, 211);
$cream: rgb(250, 249, 246);
$beige: rgb(200, 183, 166);
$periwinkle: rgb(125, 148, 181);
$dustyrose: rgb( 194, 149, 145);
