.search-bar {
  background-color: $white !important;
  border-radius: 0.9rem;
  border: none;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  :focus {
    box-shadow: 0 0 0 0.2rem rgb(74, 62, 66, 25%);
  }
}
